import { v4 as uuidv4 } from 'uuid';

// 单例模式
export const uuid_token = () => {
  // 获取本地uuidInfo
  let uuidInfo = localStorage.getItem('UUIDINFO')
  // 如果本地没有uuid
  if (!uuidInfo) {
    // 创建新的uuid
    uuidInfo = uuidv4()
    // 并存入本地存储
    localStorage.setItem('UUIDINFO', uuidInfo)
  }
  // 返回uuid
  return uuidInfo
}