import { reqCartList, reqRemoveShopCart, updateChecked } from "@/api"

const state = {
  shopCartList: []
}
const mutations = {
  GETSHOPCARTLIST(state, shopCartList) {
    if (shopCartList[0]) {
      state.shopCartList = shopCartList[0].cartInfoList
    }else{
      state.shopCartList = []
    }
  }
}
const actions = {
  async getShopCartList({ commit }) {
    let result = await reqCartList()
    if (result.code == 200) {
      commit('GETSHOPCARTLIST', result.data)
    }
  },
  async removeShopCart({ commit }, skuId) {
    let result = await reqRemoveShopCart(skuId)
    if (result.code == 200) {
      return 'ok'
    } else {
      return Promise.reject(new Error('fail'))
    }
  },
  async updateCheckedStatus({ commit }, { skuId, isChecked }) {
    let result = await updateChecked(skuId, isChecked)
    if (result.code == 200) {
      return 'ok'
    } else {
      return Promise.reject(new Error('fail'))
    }
  },
  updateCheckedAllStatus({ dispatch, state }, haveChangeStatus) {
    let promiseAll = []
    state.shopCartList.forEach((item) => {
      if (item.isChecked != haveChangeStatus) {
        let result = dispatch('updateCheckedStatus', {
          skuId: item.skuId,
          isChecked: haveChangeStatus
        })
        promiseAll.push(result)
      }
    })
    return Promise.all(promiseAll)
  },
  deleteSelectedAllGoods({ dispatch, state }) {
    let promiseAll = []
    state.shopCartList.forEach((item) => {
      if (item.isChecked == 1) {
        let result = dispatch('removeShopCart', item.skuId)
        promiseAll.push(result)
      }
    })
    return Promise.all(promiseAll)
  }
}
const getters = {}
export default {
  state,
  mutations,
  actions,
  getters
}